<template>
  <div class="real-time-statistics view-list">
    <div class="content">
      <div class="header-sub">
        <div class="title">
          <div class="breadcrumb-item-link">历史数据统计分析</div>
        </div>
      </div>
      <div class="container-box">
        <div class="left">
          <div class="left-title">
            <Icon
              class="icon"
              type="md-search"
              style="z-index: 99"
              @click="loadTableData"
            />
            <Input
              class="serch-input"
              v-model="search.name"
              :maxlength="50"
              placeholder="请输入设备名称/编码"
              @on-enter="loadTableData"
            >
            </Input>
          </div>
          <div class="left-box">
            <li
              v-for="(item, index) in tableData"
              @click="clickLi(item, index)"
              :class="clicked === index ? 'active' : ''"
            >
              <div class="li-title">
                <div
                  class="li-left"
                  :title="item.deviceName.length > 10 ? item.deviceName : ''"
                >
                  {{ item.deviceName }}
                </div>
                <div
                  class="li-right"
                  :class="item.deviceStatus == 1 ? 'green' : 'red'"
                >
                  {{ item.deviceStatusName }}
                </div>
              </div>
              <div class="li-content">
                <div class="li-code">
                  <span> {{ $t("common.text.device_code") }}：</span
                  ><span
                    class="title"
                    :title="item.deviceCode.length > 15 ? item.deviceCode : ''"
                    >{{ item.deviceCode }}</span
                  >
                </div>
                <div class="li-code">
                  <span>{{ $t("common.text.device_model") }}：</span
                  ><span
                    class="title"
                    :title="item.modelName.length > 15 ? item.modelName : ''"
                    >{{ item.modelName }}</span
                  >
                </div>
              </div>
            </li>
          </div>
        </div>
        <div class="right">
          <div class="right-title">选择数据项:</div>
          <div class="right-box">
            <div class="item-list">
              <div
                class="item"
                v-for="item in realData"
                :key="item.itemId"
                :class="currentId === item.itemId ? 'item-active' : ''"
                @click="checkItemId(item)"
              >
                <div class="item-top">{{ item.itemName }}</div>
                <div class="item-bottom">
                  <div
                    style="color:#fff;word-break: break-all;overflow: hidden;text-overflow: ellipsis;display: -webkit-box;-webkit-box-orient: vertical;-webkit-line-clamp: 1;"
                    >{{ item.alias }}</div
                  >
                </div>
              </div>
            </div>
          </div>
          <div class="chart-title">
            <DatePicker
              class="real-time-data"
              type="datetimerange"
              placeholder="日期范围:起始时间                       ~       截止时间"
              v-model="rangeTime"
              style="width: 380px;margin-right: 20px"
            ></DatePicker>
            <Button
              type="default"
              @click="loadLineData"
              :loading="pageInfo.loading"
            >
              <span class="iconfont icon-chazhao"></span>
            </Button>
          </div>
          <div class="chart-box" v-if="chartOption !== {}">
            <ChartBase id="lineEChart" :options="chartOption"></ChartBase>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import ChartBase from '@/components/chart-base'
import {
  getDeviceList,
  lineItemsData,
  getHistoryPoint
} from '../../../../api/device'
import moment from 'moment'

export default {
  name: 'real-time-statistics',
  components: { ChartBase },
  data () {
    return {
      chartOption: {},
      search: {
        name: '',
        status: ''
      },
      // baseInfo: {},
      currentId: '',
      rangeTime: [
        moment()
          .subtract(1, 'days')
          .format('YYYY-MM-DD HH:mm:ss'),
        moment().format('YYYY-MM-DD HH:mm:ss')
      ],
      tableColumns: [
        {
          title: this.$t_p('device_name'),
          key: 'deviceName',
          align: 'center'
        },
        {
          title: this.$t('common.text.device_code'),
          key: 'deviceCode',
          align: 'center'
        },
        {
          title: this.$t('common.text.device_model'),
          key: 'modelName',
          align: 'center'
        },
        {
          title: this.$t('common.text.device_state'),
          key: 'deviceStatusName',
          align: 'center',
          render: (h, params) => {
            return h(
              'span',
              {
                class: params.row.deviceStatus == 1 ? 'success' : 'warning'
              },
              params.row.deviceStatusName
            )
          }
        }
      ],
      deviceId: '',
      tableData: [],
      deviceName: [],
      realData: [],
      clicked: 0,
      pageInfo: {
        loading: false,
        pageIndex: 1,
        pageSize: 10000,
        totalElements: 0
      }
    }
  },
  watch: {
    deviceId (v) {
      this.itemIds = []
      this.chartOption = {}
      this.loadRealData()
    }
  },
  methods: {
    clickLi (item, index) {
      this.clicked = index
      this.deviceId = item.id
    },
    checkDate (item) {
      item.checked = !item.checked
      if (item.checked) {
        this.itemIds.push(item.itemId)
        this.deviceName.push(item.alias)
      } else {
        this.itemIds.map((d, index) => {
          if (item.itemId == d) {
            this.itemIds.splice(index, 1)
            this.deviceName.splice(index, 1)
          }
        })
      }
    },
    checkItemId (item) {
      item.checked = !item.checked
      console.log(item)
      this.currentId = item.itemId
      this.itemIds = []
      this.itemIds.push(item.itemId)
    },

    loadRealData () {
      getHistoryPoint({
        id: this.deviceId
      }).then(res => {
        this.realData = (res.data || []).map(d => ({
          ...d,
          checked: false
        }))
      })
    },
    loadLineData () {
      let beginTime =
        this.rangeTime[0] &&
        moment(moment(this.rangeTime[0])).format('YYYY-MM-DD HH:mm:ss')
      let endTime =
        this.rangeTime[1] &&
        moment(moment(this.rangeTime[1])).format('YYYY-MM-DD HH:mm:ss')

      if (this.itemIds.length > 0 && beginTime != '' && endTime != '') {
        this.pageInfo.loading = true
        lineItemsData({
          deviceId: this.deviceId,
          itemIds: this.itemIds.toString(),
          sDate: beginTime,
          eDate: endTime
        }).then(res => {
          if (res.data) {
            let time = res.data.dataTime
            let lines = (res.data.lines || []).map(d => {
              let obj = {}
              obj = {
                ...d,
                smooth: false
              }
              return obj
            })
            this.line(time, lines, this.deviceName)
          }
          this.pageInfo.loading = false
        })
      } else {
        if (this.itemIds.length == 0) {
          this.$Message.warning('该设备没有匹配的信息')
        } else if (beginTime == '' || endTime == '') {
          this.$Message.warning('该时间段没有匹配的信息')
        }
      }
    },
    rgb () {
      var r = Math.floor(Math.random() * 256)
      var g = Math.floor(Math.random() * 256)
      var b = Math.floor(Math.random() * 256)
      var rgb = '(' + r + ',' + g + ',' + b + ')'
      return rgb
    },
    color () {
      // 十六进制颜色随机
      var r = Math.floor(Math.random() * 256)
      var g = Math.floor(Math.random() * 256)
      var b = Math.floor(Math.random() * 256)
      var color = '#' + r.toString(16) + g.toString(16) + b.toString(16)
      return color
    },
    line: function (time, itemDatas, deviceName) {
      this.chartOption = {
        grid: {
          x: 80,
          y: 20,
          x2: 80,
          y2: 50,
          containLabel: true
        },
        // legend: {
        //   data:deviceName,
        //   x: 'center',
        //   y: 'top'
        // },
        tooltip: {
          trigger: 'axis'
        },
        color: [
          'rgba(0, 132, 255, 1)',
          '#87cefa',
          '#da70d6',
          '#32cd32',
          '#6495ed',
          '#ff69b4',
          '#ba55d3',
          '#cd5c5c',
          '#ffa500',
          '#40e0d0',
          '#1e90ff',
          '#ff6347',
          '#7b68ee',
          '#00fa9a',
          '#ffd700',
          '#6699FF',
          '#ff6666',
          '#3cb371',
          '#b8860b',
          '#30e0e0'
        ],
        // dataZoom: [
        //   {
        //     show: true,
        //     realtime: true,
        //     backgroundColor:"rgba(47,69,84,0)",
        //     borderColor:"#ddd",
        //     fillerColor:"rgba(167,183,204,0.4)",
        //     zoomOnMouseWheel:true,
        //     moveOnMouseMove:true,
        //     bottom:"2%",
        //   }
        // ],
        xAxis: {
          type: 'category',
          boundaryGap: false,
          data: time,
          // x轴的颜色和宽度
          splitLine: {
            show: false,
            lineStyle: {
              color: ['#dedede']
            }
          },

          axisLine: {
            lineStyle: {
              color: '#d2d2d2',
              width: 1 // 这里是坐标轴的宽度,可以去掉
            }
          }
        },
        dataZoom: [
          {
            // type: "slider"
            type: 'slider', // 这个 dataZoom 组件是 inside 型 dataZoom 组件（能在坐标系内进行拖动，以及用滚轮（或移动触屏上的两指滑动）进行缩放）
            xAxisIndex: 0, // 控制x轴
            start: 0,
            end: 100
          }
        ],
        yAxis: {
          type: 'value',
          splitLine: {
            show: true,
            lineStyle: {
              color: ['#aaa']
            }
          },
          axisLine: {
            lineStyle: {
              color: '#d2d2d2',
              width: 1 // 这里是坐标轴的宽度
            }
          }
        },
        series: itemDatas
      }
      // itemChart.setOption(option);
    },

    loadTableData (clear) {
      if (clear) {
        this.pageInfo.pageIndex = 1
      }
      const DeviceStatusMap = {
        1: this.$t('common.text.run'),
        0: this.$t('common.text.stop')
      }

      getDeviceList({
        pageNumber: this.pageInfo.pageIndex,
        pageSize: this.pageInfo.pageSize,
        name: this.search.name,
        deviceStatus: this.search.status
      })
        .then(res => {
          this.tableData = (res.data.result || []).map(d => ({
            ...d,
            deviceStatusName: DeviceStatusMap[d.deviceStatus]
          }))
          if (this.tableData.length > 0) {
            this.deviceId = this.tableData[0].id
          }
          this.pageInfo.totalElements = res.data.totalElements || 0
        })
        .finally(() => {
          // this.pageInfo.loading = false;
        })
    },
    onPaginationChange (index) {
      this.pageInfo.pageIndex = index
      this.loadTableData(false)
    },
    onPaginationSizeChange (size) {
      this.pageInfo.pageSize = size
      this.loadTableData(true)
    }
  },
  mounted () {
    this.loadTableData(true)
    // this.loadRealData()
    // this.loadLineData()
  }
}
</script>

<style lang="less" scoped>
@import "../../../../style/varient";

@size-padding: 10px;
/deep/.ivu-date-picker {
  input {
    width: 390px !important;
  }
}
.real-time-statistics {
  width: 100%;
  height: 100%;
  .content {
    width: 100%;
    height: 100%;
    box-shadow: 0px -1px 2px 0px rgba(0, 0, 0, 0.05),
      /*上边阴影  红色*/ -1px 0px 2px 0px rgba(0, 0, 0, 0.05),
      /*左边阴影  绿色*/ 1px 0px 2px 0px rgba(0, 0, 0, 0.05),
      /*右边阴影  蓝色*/ 0px 1px 2px 0px rgba(0, 0, 0, 0.05); /*下边阴影  黄色*/
    padding: 24px;
  }
}

.real-time-statistics {
  height: 100%;
  padding: 0 20px 20px 20px;

  .green {
    color: @color-success;
  }

  .red {
    color: @color-warning;
  }

  .container-box {
    width: 100%;
    height: 100%;
    /*background: black;*/
    display: flex;

    .left {
      width: 240px;
      padding-right: 10px;
      height: 100%;
      overflow-y: auto;
      /*background: rgb(16, 86, 244);*/

      .left-title {
        /*position: relative;*/
        position: sticky;
        top: 0;
        // background: white;
        background: rgba(0, 0, 0, 1);
        .label {
          padding: 5px 10px;
        }
        span {
          /*font-weight: bold;*/
          font-size: 14px;
        }

        .icon {
          position: absolute;
          bottom: 8px;
          right: 10px;
          color: rgba(255, 255, 255, 0.3);
          font-size: 12px;
        }

        .icon:hover {
          cursor: pointer;
        }

        .serch-input {
          margin-top: 0;
          background: #262c3a;
        }
      }

      .left-box {
        /*height: 120%;*/
        width: 100%;

        li {
          list-style: none;
          margin: 10px 0;
          // border: 1px solid #d2d2d2;
          border: 1px solid rgba(0, 0, 0, 0.6);

          .li-title {
            height: 35px;
            // color: black;
            color: #eee;
            line-height: 35px;
            // background: #eee;
            background: #000;
            display: flex;
            padding: 0 10px;

            .li-left {
              /*color: black;*/
              flex: 1;
              font-size: 14px;
              overflow: hidden;
              /*font-weight: bold;*/
              text-overflow: ellipsis;
              white-space: nowrap;
            }

            .li-right {
              text-align: right;
              font-size: 12px;
            }
          }

          .li-content {
            padding: 10px;
            font-size: 12px;

            .li-code {
              height: 28px;
              line-height: 28px;
              /*color: #999;*/
              display: flex;

              span:first-child {
                color: #888;
                // width: 60px;
                /*font-weight: bold;*/
              }

              span:last-child {
                color: #999;
                flex: 1;
                overflow: hidden;
                text-overflow: ellipsis;
                white-space: nowrap;
              }
            }
          }
        }

        li:hover {
          cursor: pointer;
        }

        .active {
          border-color: rgba(0, 132, 255, 0.5);
          box-shadow: 0px 6px 12px rgba(0, 132, 255, 0.1);
          .li-title {
            background-color: rgba(0, 132, 255, 1);
          }
          .li-left {
            color: rgba(255, 255, 255, 1);
          }
        }
      }
    }

    .right {
      flex: 1;
      height: 100%;
      overflow-y: auto;
      padding-left: 15px;

      .right-title {
        color: #fff;
      }

      .right-box {
        width: 100%;

        .item-list {
          max-height: 260px;
          overflow: scroll;
          margin-top: 5px;
          display: flex;
          flex-wrap: wrap;
          background: #000;
          border-radius: 5px;

          .item {
            width: calc(~"20% - " 22px);
            margin: 10px;
            position: relative;
            cursor: pointer;

            &-top {
              text-align: center;
              height: 32px;
              padding-left: 12px;
              border: 1px solid rgba(0, 0, 0, 0.10196078431372549);
              background-color: rgba(33, 34, 39, 0.6);
              // color: rgba(0, 0, 0, 1);
              color: #fff;
              font-size: 14px;
              line-height: 32px;
            }
            &-bottom {
              height: 76px;
              border: 1px solid rgba(0, 0, 0, 0.10196078431372549);
              border-top: none;
              // background-color: #fff;
              background-color: #262c3a;
              line-height: 76px;
              text-align: center;
              span {
                &:first-child {
                  margin-right: 8px;
                  color: #eee;
                  font-size: 18px;
                  font-family: Alibaba PuHuiTi;
                  font-weight: 400;
                }
                &:last-child {
                  color: #888;
                  font-size: 12px;
                  font-family: Alibaba PuHuiTi;
                  font-weight: 400;
                }
              }
            }

            .item:hover {
              cursor: pointer;
            }
          }

          .item-active {
            box-shadow: 0px 6px 12px rgba(0, 132, 255, 0.1);
            .item-top {
              background-color: rgba(0, 132, 255, 1);
              color: rgba(255, 255, 255, 1);
            }
            .itme-top,
            .item-bottom {
              border-color: rgba(0, 132, 255, 0.5);
            }
          }
        }
      }

      .chart-title {
        margin-top: 10px;
        display: flex;
      }

      .chart-box {
        margin-top: 30px;
        width: 100%;
        height: 420px;
        .chartBase {
          width: 100%;
          height: 100%;
        }

        #lineEChart {
          width: 100%;
          height: 100%;
        }
      }
    }

    /*滚动条样式*/
    /*.right:hover{*/
    /*  overflow-y: auto;*/
    /*}*/

    .left::-webkit-scrollbar,
    .right::-webkit-scrollbar,
    .right-box::-webkit-scrollbar {
      /*滚动条整体样式*/
      width: 6px; /*高宽分别对应横竖滚动条的尺寸*/
      height: 8px;
    }

    .left::-webkit-scrollbar-thumb,
    .right::-webkit-scrollbar-thumb,
    .right-box::-webkit-scrollbar-thumb {
      /*滚动条里面小方块*/
      /*border-radius: 5px;*/
      // -webkit-box-shadow: inset 0 0 5px rgba(114, 104, 104, 0.8);
      background: #eee;
    }

    .left::-webkit-scrollbar-track,
    .right::-webkit-scrollbar-track,
    .right-box::-webkit-scrollbar-track {
      /*滚动条里面轨道*/
      // -webkit-box-shadow: inset 0 0 5px rgba(114, 104, 104, 0.8);
      border-radius: 0;
      background: transparent;
    }
  }

  .table-filters {
    display: inline-block;

    .filter-item {
      display: inline-block;
      margin-right: 20px;

      .label {
        font-size: @size-font-normal;
        /*font-weight: normal;*/
        /*font-stretch: normal;*/
        /*letter-spacing: 1px;*/
        color: #1f1f1f;
        padding: 0 9px;
        max-height: 7px;
        display: inline-block;
        min-width: 4em;
      }

      .filter-input {
        width: 170px;
        font-size: @size-font-normal;
      }
    }
  }

  .header {
    height: 60px;
    line-height: 60px;
  }

  .content {
    position: relative;
    padding-bottom: 20px;

    .user-table {
      /*border:1px solid #d2d2d2;*/

      .ivu-table-row.even {
        background-color: #e5f5fe;
      }
    }

    .pagination {
      margin: 20px 0;
      text-align: right;
    }
  }
}

.item-list {
  /*overflow: auto;*/

  .item {
    /*position: relative;*/
    /*margin-right: 20px;*/
    /*margin-bottom: 20px;*/
    /*float: left;*/
    /*border-radius: 4px;*/
    /*overflow: hidden;*/

    .footer {
      /*color: rgb(16, 86, 244);*/
      /*font-size: 12px;*/
      /*display: flex;*/
      /*text-align: center;*/
      /*position: absolute;*/
      /*bottom: 20px;*/
      /*left: 0;*/
      /*right: 0;*/
      /*cursor: pointer;*/

      .item-text {
        /*text-align: center;*/
        /*height: 100%;*/
        /*width: 100%;*/

        /*color: #2db7f5;*/

        .val {
          /*width: 205px;*/
          /*height: 120px;*/
          /*background-image: linear-gradient(0deg,*/
          /*#f5f5f5 0%,*/
          /*#ffffff 100%),*/
          /*linear-gradient(*/
          /*  #ffffff,*/
          /*  #ffffff);*/
          /*background-blend-mode: normal, normal;*/
          /*border-radius: 5px;*/
          /*border: solid 1px #dcdcdc;*/
          /*box-sizing: border-box;*/
          /*position: absolute;*/
          /*left: 50%;*/
          /*margin-left: -102.5px;*/
          /*top: 30px;*/

          /*text-align: center;*/

          /*font-size: 30px;*/
          /*font-weight: normal;*/
          /*font-stretch: normal;*/
          /*line-height: 1;*/
          /*letter-spacing: 2px;*/
          /*color: rgb(16, 86, 244);*/
          /*padding-top: 40px;*/

          /*.unit {*/
          /*  font-size: 14px;*/
          /*  font-weight: normal;*/
          /*  line-height: 20px;*/
          /*  color: #787878;*/
          /*}*/
        }

        /*.name {*/
        /*  position: absolute;*/
        /*  bottom: 60px;*/
        /*  left: 0;*/
        /*  right: 0;*/
        /*  font-size: 14px;*/
        /*  font-stretch: normal;*/
        /*  text-align: center;*/
        /*  font-weight: bold;*/
        /*  color: #1f1f1f;*/
        /*}*/
      }

      /*.btn-to-trace {*/
      /*  flex: 1;*/
      /*  height: 14px;*/
      /*  line-height: 14px;*/
      /*  border-right: 1px solid #dcdcdc;*/
      /*}*/

      /*.btn-to-history {*/
      /*  flex: 1;*/
      /*  height: 14px;*/
      /*  line-height: 14px;*/
      /*}*/
    }

    /*&:hover {*/
    /*  box-shadow: 0 0 5px #dcdcdc;*/
    /*}*/
  }
}
</style>



